html { overflow: hidden; height: 100%; }

body {
    height: 100%;
    min-height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    background-color:  rgb(217, 233, 235);
}

input {
    cursor: pointer;
    outline: none;
}

.container {
    position: relative;
    width: 100%;
    height: 100%;
    max-width: 90vw;
    margin: 0 auto;
}

.flex-row { 
    display: flex;
    flex-direction: row;
    align-items: center;
}


.flex-column {
    display: flex;
    flex-direction: column;
    align-items: center;
}


.col-12 {
    flex: 0 0 100%;
}

.col-6 {
    flex: 0 0 50%;
}

    .fade {
        animation: transitionComponents .3s linear;
    }

    @keyframes transitionComponents {
        from { 
            opacity: 0.3;
         }
        to {
           opacity: 1;
        }
    }


